.legend {
  list-style: none;
  font-size: 0.6em;
}
.legend li {
  float: left;
  margin-right: 10px;
}
.legend span {
  border: 1px solid #ccc;
  float: left;
  width: 12px;
  height: 12px;
  margin: 2px;
}
/* your colors */
.legend .Metallurgical-legend-item {
  background-color: gray;
}
.legend .Thermal-legend-item {
  background-color: brown;
}
.legend .Unspecified-legend-item {
  background-color: black;
}
